}

.it-solutions-projects-section-1 .btn-filter {
  border: none;
  border-radius: 0;
  text-transform: uppercase;
  color: var(--tc-system-black);
}
.it-solutions-projects-section-1 .btn-filter.active {
  color: var(--tc-theme-primary);
  text-decoration: underline;
}
.it-solutions-projects-section-1 .masonary-active .card-news {
  margin: 15px 0;
}
.it-solutions-projects-section-1 .grid-sizer,
.it-solutions-projects-section-1 .grid-item {
  width: 50%;
}
@media (min-width: 1400px) {
  .it-solutions-projects-section-1 .grid-sizer,
  .it-solutions-projects-section-1 .grid-item {
    width: 50%;
  }
